'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Checkbox } from '@/components/ui/checkbox';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';

interface AttendanceRecord {
  studentId: string;
  studentName: string;
  enrollmentNo: string;
  isPresent: boolean;
}

interface AttendanceSession {
  id: string;
  date: string;
  course: string;
  subject: string;
  records: AttendanceRecord[];
}

const sampleStudents = [
  { id: '1', name: 'John Doe', enrollmentNo: 'CS2021001' },
  { id: '2', name: 'Jane Smith', enrollmentNo: 'CS2021002' },
  { id: '3', name: 'Mike Johnson', enrollmentNo: 'EE2021001' },
  { id: '4', name: 'Sarah Wilson', enrollmentNo: 'CS2021003' },
  { id: '5', name: 'David Brown', enrollmentNo: 'ME2021001' }
];

export default function AttendancePage() {
  const { user } = useAuth();
  const router = useRouter();
  const [attendanceSessions, setAttendanceSessions] = useState<AttendanceSession[]>([]);
  const [currentSession, setCurrentSession] = useState<AttendanceRecord[]>([]);
  const [selectedCourse, setSelectedCourse] = useState('');
  const [selectedSubject, setSelectedSubject] = useState('');
  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().split('T')[0]);
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');
  const [isMarkingAttendance, setIsMarkingAttendance] = useState(false);

  useEffect(() => {
    if (user && !['admin', 'teacher', 'owner'].includes(user.role)) {
      router.push('/dashboard');
    }
  }, [user, router]);

  const initializeSession = () => {
    if (!selectedCourse || !selectedSubject) {
      setError('Please select course and subject');
      return;
    }

    const records: AttendanceRecord[] = sampleStudents.map(student => ({
      studentId: student.id,
      studentName: student.name,
      enrollmentNo: student.enrollmentNo,
      isPresent: false
    }));

    setCurrentSession(records);
    setIsMarkingAttendance(true);
    setError('');
  };

  const toggleAttendance = (studentId: string) => {
    setCurrentSession(prev => prev.map(record => 
      record.studentId === studentId 
        ? { ...record, isPresent: !record.isPresent }
        : record
    ));
  };

  const markAllPresent = () => {
    setCurrentSession(prev => prev.map(record => ({ ...record, isPresent: true })));
  };

  const markAllAbsent = () => {
    setCurrentSession(prev => prev.map(record => ({ ...record, isPresent: false })));
  };

  const saveAttendance = () => {
    const newSession: AttendanceSession = {
      id: Date.now().toString(),
      date: selectedDate,
      course: selectedCourse,
      subject: selectedSubject,
      records: currentSession
    };

    setAttendanceSessions(prev => [newSession, ...prev]);
    setMessage('Attendance saved successfully!');
    setIsMarkingAttendance(false);
    setCurrentSession([]);
    setTimeout(() => setMessage(''), 3000);
  };

  const cancelSession = () => {
    setIsMarkingAttendance(false);
    setCurrentSession([]);
  };

  if (!user || !['admin', 'teacher', 'owner'].includes(user.role)) {
    return (
      <div className="text-center py-8">
        <h1 className="text-2xl font-bold text-gray-900 mb-4">Access Denied</h1>
        <p className="text-gray-600">You don't have permission to access this page.</p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Attendance Management
        </h1>
        <p className="text-gray-600">
          Mark and manage student attendance for classes.
        </p>
      </div>

      {message && (
        <Alert>
          <AlertDescription className="text-green-700">{message}</AlertDescription>
        </Alert>
      )}

      {error && (
        <Alert variant="destructive">
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {!isMarkingAttendance ? (
        <Card>
          <CardHeader>
            <CardTitle>Start New Attendance Session</CardTitle>
            <CardDescription>
              Select course and subject to begin marking attendance
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="space-y-2">
                <Label htmlFor="date">Date</Label>
                <Input
                  id="date"
                  type="date"
                  value={selectedDate}
                  onChange={(e) => setSelectedDate(e.target.value)}
                />
              </div>

              <div className="space-y-2">
                <Label htmlFor="course">Course</Label>
                <Select value={selectedCourse} onValueChange={setSelectedCourse}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select course" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Computer Science">Computer Science</SelectItem>
                    <SelectItem value="Electrical Engineering">Electrical Engineering</SelectItem>
                    <SelectItem value="Mechanical Engineering">Mechanical Engineering</SelectItem>
                    <SelectItem value="Civil Engineering">Civil Engineering</SelectItem>
                  </SelectContent>
                </Select>
              </div>

              <div className="space-y-2">
                <Label htmlFor="subject">Subject</Label>
                <Select value={selectedSubject} onValueChange={setSelectedSubject}>
                  <SelectTrigger>
                    <SelectValue placeholder="Select subject" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Data Structures">Data Structures</SelectItem>
                    <SelectItem value="Database Management">Database Management</SelectItem>
                    <SelectItem value="Operating Systems">Operating Systems</SelectItem>
                    <SelectItem value="Computer Networks">Computer Networks</SelectItem>
                    <SelectItem value="Software Engineering">Software Engineering</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>

            <Button onClick={initializeSession} className="w-full">
              Start Attendance Session
            </Button>
          </CardContent>
        </Card>
      ) : (
        <Card>
          <CardHeader>
            <div className="flex justify-between items-center">
              <div>
                <CardTitle>Mark Attendance</CardTitle>
                <CardDescription>
                  {selectedCourse} - {selectedSubject} | {new Date(selectedDate).toLocaleDateString()}
                </CardDescription>
              </div>
              <div className="flex space-x-2">
                <Button variant="outline" size="sm" onClick={markAllPresent}>
                  Mark All Present
                </Button>
                <Button variant="outline" size="sm" onClick={markAllAbsent}>
                  Mark All Absent
                </Button>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>Enrollment No</TableHead>
                  <TableHead>Student Name</TableHead>
                  <TableHead>Present</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {currentSession.map((record) => (
                  <TableRow key={record.studentId}>
                    <TableCell className="font-medium">{record.enrollmentNo}</TableCell>
                    <TableCell>{record.studentName}</TableCell>
                    <TableCell>
                      <Checkbox
                        checked={record.isPresent}
                        onCheckedChange={() => toggleAttendance(record.studentId)}
                      />
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>

            <div className="flex justify-end space-x-2 mt-6">
              <Button variant="outline" onClick={cancelSession}>
                Cancel
              </Button>
              <Button onClick={saveAttendance}>
                Save Attendance
              </Button>
            </div>
          </CardContent>
        </Card>
      )}

      {attendanceSessions.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Recent Attendance Sessions</CardTitle>
            <CardDescription>
              Previously recorded attendance sessions
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {attendanceSessions.map((session) => {
                const presentCount = session.records.filter(r => r.isPresent).length;
                const totalCount = session.records.length;
                const attendancePercentage = Math.round((presentCount / totalCount) * 100);

                return (
                  <div key={session.id} className="border rounded-lg p-4">
                    <div className="flex justify-between items-center mb-2">
                      <div>
                        <h3 className="font-semibold">{session.course} - {session.subject}</h3>
                        <p className="text-sm text-gray-600">
                          {new Date(session.date).toLocaleDateString()}
                        </p>
                      </div>
                      <div className="text-right">
                        <div className="text-lg font-semibold">{attendancePercentage}%</div>
                        <div className="text-sm text-gray-600">
                          {presentCount}/{totalCount} present
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  );
}
